import React from "react";
import Axios from "axios";
import "./App.css"

export default class App extends React.Component{
    state={
        confirmed:0,
        recovered:0,
        death:0, 
        country: [""]
    }
    componentDidMount(){
        this.getData();
    }

    async getData(){
        const res=await Axios.get("https://covid19.mathdro.id/api");
        const resCountry=await Axios.get("https://covid19.mathdro.id/api/countries");
        const resco=resCountry.data;
      //  console.log(resco);
        //console.log(resco.countries);
        var countryN= [];
       // alert("good");
        // eslint-disable-next-line array-callback-return
        resco.countries.map((dat)=> {
           // alert("good1");
            countryN.push(dat.name);
            //return <div>dat.countries.name;</div>
        });
        this.setState({
            confirmed: res.data.confirmed.value,
            recovered: res.data.recovered.value,
            death: res.data.deaths.value,
            country: countryN
        }); 
    }

    renderCountryOption(){
        return this.state.country.map((dat,i)=>{
            return <option key={i}>{dat}</option>
        });
        
    }

    async myFunction(e){
        //console.log(e.target.value);
        //alert("in myFunction");
        
        const res=await Axios.get(`https://covid19.mathdro.id/api/countries/${e.target.value}`);
        //console(res);
        this.setState({
            confirmed: res.data.confirmed.value,
            recovered: res.data.recovered.value,
            death: res.data.deaths.value
        }); 
    }

    render(){
        
        return(
            <div className="mainDiv">
                <h1>Corona update</h1>

                <select id="muSelect" onChange={this.myFunction.bind(this)}>
                    <option>country</option>
                    {this.renderCountryOption()}
                </select>

                <div className="dataClass">
                    <div className="confirmed">
                        <h3>total confirmed case</h3>
                        <h4>{this.state.confirmed}</h4>
                    </div>
                    <div>
                        <h3>total recovered case</h3>
                        <h4>{this.state.recovered}</h4>
                    </div>
                    <div>
                        <h3>total death case</h3>
                        <h4>{this.state.death}</h4>
                    </div>
                </div>
            </div>
        )
    }
}
